import React from "react";
import { injectIntl } from "react-intl";
import { Row, Col, Icon, Tooltip, Card, message, Spin, Skeleton } from "antd";
import {
  ChartCard,
  Field,
  MiniArea,
  MiniBar,
  MiniProgress,
  Bar,
  Pie
} from "ant-design-pro/lib/Charts";
import Trend from "ant-design-pro/lib/Trend";
import NumberInfo from "ant-design-pro/lib/NumberInfo";

import numeral from "numeral";
import moment from "moment";
import styled from "styled-components";

import { getUserDashboard } from "./../../services";

const ColWrapper = styled(Col)`
  padding-left: 12px;
  padding-right: 12px;
  margin-bottom: 24px;
`;

class UserDashboard extends React.Component {
  _isMounted = false;

  state = {
    loading: true,
    data: {}
  };

  componentDidMount = () => {
    this.fetchData();
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  fetchData = async () => {
    this._isMounted = true;
    try {
      const response = await getUserDashboard();
      if (this._isMounted) {
        this.setState({
          data: response.data.data,
          loading: false
        });
      }
    } catch (error) {
      message.error(error.message);
      if (this._isMounted) {
        this.setState({
          loading: false
        });
      }
    }
  };

  render() {
    const {
      intl: { messages }
    } = this.props;
    const { data } = this.state;

    const monthProcessData = [];
    let countProcess = 0;
    let countProcessLW = 0;
    const monthExpensesData = [];
    let countExpenses = 0;
    let countExpensesLW = 0;

    const visitData = [];
    const processData = [];
    const beginDay = new Date().getTime();
    let countVisit = 0;
    let countVisitLW = 0;

    if (data && data.weekProcess) {
      data.weekProcess.map((item, i) => {
        if (i >= 23) {
          countProcessLW = countProcessLW + item.count;
        }
        monthProcessData.push({
          x: moment(new Date(item.start)).format("YYYY-MM-DD"),
          y: Math.floor(item.count)
        });
      });
      countProcess = data.countProcess;

      data.weekExpenses.map((item, i) => {
        if (i >= 23) {
          countExpensesLW = countExpensesLW + item.value;
        }
        monthExpensesData.push({
          x: moment(new Date(item.start)).format("YYYY-MM-DD"),
          y: item.value.toFixed(2)
        });
      });
      countExpenses = data.totalExpenses;
    }

    for (let i = 0; i < 20; i += 1) {
      let y = Math.floor(Math.random() * 10) + 10;
      let yy = Math.floor(Math.random() * 10) + 10;
      countVisit = countVisit + y;
      if (20 - i <= 7 && 20 - i >= 0) {
        countVisitLW = countVisitLW + y;
      }
      visitData.push({
        x: moment(new Date(beginDay + 1000 * 60 * 60 * 24 * i)).format(
          "YYYY-MM-DD"
        ),
        y
      });
      processData.push({
        x: moment(new Date(beginDay + 1000 * 60 * 60 * 24 * i)).format(
          "YYYY-MM-DD"
        ),
        y: yy
      });
    }
    let countVisitDay = countVisit / 20;

    const salesData = [];
    for (let i = 0; i < 12; i += 1) {
      salesData.push({
        x: `${i + 1}`,
        y: Math.floor(Math.random() * 1000) + 200
      });
    }

    const salesPieData = [
      {
        x: "Translation",
        y: 4.54
      },
      {
        x: "Summarization",
        y: 3.32
      },
      {
        x: "Anonymization",
        y: 3.11
      },
      {
        x: "Classification",
        y: 2.34
      }
    ];

    return (
      <Spin spinning={this.state.loading}>
        <Row style={{ margin: "12px -12px 0 -12px" }}>
          <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
            {this.state.loading ? (
              <Card>
                <Skeleton active />
              </Card>
            ) : (
              <ChartCard
                title={messages.dashboard.numberOfProcesses.toUpperCase()}
                total={numeral(countProcess).format("0,0")}
                contentHeight={134}
              >
                <NumberInfo
                  subTitle={
                    <span>
                      {messages.dashboard.processesThisWeek.toUpperCase()}
                    </span>
                  }
                  total={numeral(countProcessLW).format("0,0")}
                />
                <MiniArea line height={45} data={monthProcessData} />
              </ChartCard>
            )}
          </ColWrapper>
          <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
            {this.state.loading ? (
              <Card>
                <Skeleton
                  active
                  title={false}
                  loading={this.state.loading}
                  paragraph={{ rows: 4 }}
                />
              </Card>
            ) : (
              <ChartCard
                title={messages.dashboard.totalExpenses.toUpperCase()}
                action={
                  <Tooltip
                    title={messages.dashboard.descriptionOfTheIndicator.toUpperCase()}
                  >
                    <Icon type="info-circle-o" />
                  </Tooltip>
                }
                total={numeral(countExpenses).format("0.000")}
                footer={
                  <Field
                    label={messages.dashboard.expensesthisWeek.toUpperCase()}
                    value={numeral(countExpensesLW).format("0.000")}
                  />
                }
                contentHeight={46}
              >
                <MiniBar height={46} data={monthExpensesData} />
              </ChartCard>
            )}
          </ColWrapper>
          <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
            <ChartCard
              title={messages.dashboard.processesCompleted.toUpperCase()}
              action={
                <Tooltip
                  title={messages.dashboard.descriptionOfTheIndicator.toUpperCase()}
                >
                  <Icon type="info-circle-o" />
                </Tooltip>
              }
              total="78%"
              footer={
                <div>
                  <span>
                    {messages.dashboard.lastMonth.toUpperCase()}
                    <Trend
                      flag="up"
                      style={{ marginLeft: 8, color: "rgba(0,0,0,.85)" }}
                    >
                      12%
                    </Trend>
                  </span>
                </div>
              }
              contentHeight={46}
            >
              <MiniProgress percent={78} strokeWidth={8} target={80} />
            </ChartCard>
          </ColWrapper>
          <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
            <ChartCard
              title={messages.dashboard.numberOfProcesses.toUpperCase()}
              total={numeral(31).format("0,0")}
              contentHeight={134}
            >
              <NumberInfo
                subTitle={
                  <span>
                    {messages.dashboard.processesThisWeek.toUpperCase()}
                  </span>
                }
                total={numeral(17).format("0,0")}
                status="up"
                subTotal={31 - 17}
              />
              <MiniArea line height={45} data={processData} />
            </ChartCard>
          </ColWrapper>
        </Row>
        <Row>
          <ColWrapper xs={24} sm={13} md={13} lg={13} xl={13}>
            <Card style={{ height: 350 }}>
              <Bar
                height={200}
                title={messages.dashboard.salesTrend.toUpperCase()}
                data={salesData}
              />
            </Card>
          </ColWrapper>
          <ColWrapper xs={24} sm={11} md={11} lg={11} xl={11}>
            <Card>
              <Pie
                hasLegend
                title={messages.dashboard.process.toUpperCase()}
                subTitle={messages.dashboard.process.toUpperCase()}
                total={() => (
                  <React.Fragment>
                    <Icon type="euro" />
                    {numeral(
                      salesPieData.reduce((pre, now) => now.y + pre, 0)
                    ).format("0.0[0000]")}
                  </React.Fragment>
                )}
                data={salesPieData}
                height={265}
              />
            </Card>
          </ColWrapper>
        </Row>
      </Spin>
    );
  }
}

export default injectIntl(UserDashboard);
